.header {
    display: flex;
    flex-direction: column;
    height: 99rem;
    background: url(../../img/header5.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: $color-white;

    &__top {
        min-height: 20rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .logo {
            display: flex;
            flex-direction: row;
            height: 11rem;
            align-items: flex-end;

            &__title {
                font-size: 6rem;
                font-weight: 900;
                margin: 0 0 0 5.3rem;
            }

            &__img {
                width: 1.4rem;
                height: 1.4rem;
                margin-bottom: 1.2rem;
            }
        }

        .nav {
            display: flex;
            align-items: center;

            &__list {
                list-style: none;
                display: flex;
                width: 100rem;
                justify-content: space-around;
                margin-left: auto;
                margin-bottom: 6rem;

                .nav__item {
                    font-size: 1.6rem;
                    text-transform: uppercase;
                    cursor: pointer;
                    padding-bottom: .5rem;
                    border-bottom: 2px solid transparent;

                    &:hover {
                        color: $color-black;
                        transition: all .2s;
                        font-weight: bold;
                        padding-bottom: .5rem;
                        border-bottom: 2px solid $color-white;
                    }
                }
            }
        }
    }

    &__promo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .header__title {
            text-align: center;
            font-family: "Cookie", cursive;
            font-size: 11rem;
        }

        .header__subtitle {
            text-align: center;
            font-size: 3.8rem;
        }

        .header__info {
            width: 60rem;
            text-align: center;
            font-size: 1.6rem;
            margin: 5rem auto;
        }

        .btn {
            display: flex;
            justify-content: center;

            .button {
                width: 14.3rem;
                height: 4rem;
                text-transform: uppercase;
                font-size: 1.6rem;
                font-weight: bold;
                color: $color-btn-header;
                border-radius: 10rem;
                border: transparent;
                background-color: $color-white;
                cursor: pointer;
            }
        }
    }
}